.article__taxonomy {
  margin-left: unit(-1.5);
  margin-top: unit(0.5);
  padding-bottom: unit(2);
  position: relative;

  &::after {
    @include pseudo;
    background-color: $border--light;
    bottom: 0;
    height: 1px;
    left: unit(1.5);
    right: 0;
  }

  a {
    @include hover-underline;
    color: $color--rose-of-sharon;
    display: inline-block;
    font-size: 1.2rem;
    margin-left: unit(1.5);
    text-transform: uppercase;
    white-space: nowrap;
  }
}
